@import url(https://fonts.googleapis.com/css?family=Raleway|Roboto|Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  reset: all;
}
.App {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

button {
  height: 80px;
  min-height: 60px;
  width: 200px;
  font-size: 1.1rem;
  font-family: "Open Sans";
  letter-spacing: 1.1px;
  color: rgb(211, 211, 211);
  background-color: #3f3f3f;
  border: none;
  border-radius: 5px;
}

button:hover {
  cursor: pointer;
  border: rgb(211, 211, 211);
  border-style: solid;
  border-width: 1px;
}
button:focus {
  outline: none;
}

.App {
  color: #3f3f3f;
}

@media (max-width: 500px) {
  button {
    height: 50px;

    width: 150px;
    font-size: 0.8rem;
    letter-spacing: 0.8px;
  }
}

.headerAll {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgb(42, 40, 41);
}

.headerPic {
  position: fixed;
  z-index: 1;
  width: 100%;
  opacity: 0.8;
  object-fit: cover;
  text-anchor: middle;
  overflow: hidden;
}
.headerText {
  position: absolute;
  z-index: 2;
  margin-top: 30vh;
  width: 70vw;
  color: rgb(211, 211, 211);
}

.headerTitle {
  font-size: 3rem;
  letter-spacing: 3px;
  font-family: "Open Sans";
  /* background: -webkit-linear-gradient(white, orange);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
}
.headerInfo {
  font-family: "Raleway";
  font-size: 1.2rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  letter-spacing: 1.3px;
}

.scroll {
  display: flex;
  position: absolute;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: center;
  color: white;
  font-size: 2rem;
  margin: 3vh;
  padding-left: 32vw;
}
.scroll img {
  opacity: 0.85;
}
@media (max-width: 1170px) {
  .headerPic {
    height: 100%;
    object-fit: cover;
  }
}
@media (max-width: 500px) {
  .headerTitle {
    font-size: 2rem;
    letter-spacing: 2px;
  }
  .headerInfo {
    font-size: 1rem;
    margin-top: 0.8rem;
    margin-bottom: 1.5rem;
    letter-spacing: 1px;
  }
}

.resHeaderAll {
  position: relative;
  display: flex;
  width: 100vw;
  height: 30rem;
}

.resumePic {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  text-anchor: middle;
  overflow: hidden;
}
.resumeHeaderContainer {
  position: relative;
  width: 100vw;
  height: 30rem;
  background: -webkit-linear-gradient(
    transparent,
    transparent,
    rgb(74, 97, 124)
  );
  z-index: 2;
}
.resumeHeader {
  position: absolute;
  top: 25vh;
  left: 20vw;
  font-size: 5rem;
  z-index: 3;
  color: white;
}
.techTitleAll {
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 2rem;
  font-family: "Open Sans";
  font-size: 1.7rem;
  letter-spacing: 2px;
}

.techAll {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: stretch;
  padding: 2rem;
