* {
  reset: all;
}
.App {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
@import url("https://fonts.googleapis.com/css?family=Raleway|Roboto|Open+Sans");

button {
  height: 80px;
  min-height: 60px;
  width: 200px;
  font-size: 1.1rem;
  font-family: "Open Sans";
  letter-spacing: 1.1px;
  color: rgb(211, 211, 211);
  background-color: #3f3f3f;
  border: none;
  border-radius: 5px;
}

button:hover {
  cursor: pointer;
  border: rgb(211, 211, 211);
  border-style: solid;
  border-width: 1px;
}
button:focus {
  outline: none;
}

.App {
  color: #3f3f3f;
}

@media (max-width: 500px) {
  button {
    height: 50px;

    width: 150px;
    font-size: 0.8rem;
    letter-spacing: 0.8px;
  }
}
